.sg.contacto-input-wrapper {
    .contacto-input {
        border-radius: 0;
        box-shadow: 0px 0px 8px rgba(106, 110, 128, 0.15);
        border: 1px solid var(--border-color-1);
        height: 36px;
        padding-left: 12px;

        &>* {
            line-height: 20px;
        }

        .ant-input-prefix {
            margin-left: auto;
        }

        &:hover,
        &:active,
        &:focus,
        &[class*='focused'] {
            border: 1px solid var(--primary-color);
        }

        &[class*='focused'] {
            .contacto-icon {
                color: var(--primary-color);
            }
        }

        &::placeholder {
            color: var(--gray-2);
        }

        &[disabled],
        &[disabled]:hover {
            background-color: var(--gray-6);
            border: 1px solid var(--border-color-1);
            box-shadow: none;
        }

        &--readonly:not(.increase-specificity) {

            &,
            & input {

                box-shadow: none;
                background-color: var(--gray-6);
                color: var(--primary-text-color);
            }

            &:hover {
                border: 1px solid var(--border-color-1);
            }
        }

        .ant-input-suffix {
            color: var(--gray-1)
        }

        &.contacto-input--small {
            height: 28px;
        }

        &.contacto-input--large {
            height: 44px;
        }

        &.contacto-input--search-box {
            box-shadow: none;
            padding-left: 8px;
        }

        &.contacto-input--no-shadow {
            box-shadow: none;
        }

        .anticon.ant-input-clear-icon {
            font-size: 14px;
        }

        .ant-input::placeholder {
            color: var(--gray-2);
        }
    }

    .contacto-input-label-wrapper {
        margin-bottom: 4px;
        line-height: 0;
    }
}